<template>
  <div>
    <b-carousel
      id="carousel-1"
      v-model="slide"
      :interval="3000"
      controls
     
      background="#ababab"
      img-width="1024"
      img-height="480"
      style="text-shadow: 1px 1px 2px #333;"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <!-- Text slides with image -->
      <b-carousel-slide
      
        
            width="1024"
            height="480"
       
        img-src="https://bricoalacantdotcom.files.wordpress.com/2013/10/la-foto-2-6.jpg"
      ></b-carousel-slide>

      <!-- Slides with custom text -->
      <b-carousel-slide 
       width="1024"
            height="480"
      img-src="https://images-na.ssl-images-amazon.com/images/I/71PmOHDSd3L._AC_SX679_.jpg">
      
      </b-carousel-slide>

      <!-- Slides with image only -->
      <b-carousel-slide 
       width="1024"
            height="480"
      img-src="https://cdn.redcanina.es/wp-content/uploads/2020/10/03205840/cama-de-madera-para-perros-1000x500.jpg">
      </b-carousel-slide>

     
      
    </b-carousel>

  
  </div>
</template>

<script>
  export default {
    data() {
      return {
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart() {
        this.sliding = true
      },
      onSlideEnd() {
        this.sliding = false
      }
    }
  }
</script>