<template>
  <div class="p-0 m-0">
    <b-container class="p-1 m-2">
      <b-row>
        <b-col cols="4" class="p-1 m-0">
          <div class="accordion p-0 m-0" role="tablist">
            <h6 class="pb-4 m-1" style="font-family: verdana">
              <strong>Categorias:</strong>
            </h6>
            <b-card
              no-body
              class="p-0 m-0"
              v-for="(data, index) in items"
              v-bind:key="data.Categoría"
            >
              <b-card-header header-tag="header" class="p-0 m-0" role="tab">
                <b-button
                  class="tamanof"
                  block
                  v-b-toggle="'cat-' + index"
                  variant="outline-dark"
                  v-on:click="refresca(data)"
                  ><h5 align="left">
                    <strong>{{ data }}</strong>
                  </h5></b-button
                >
              </b-card-header>

              <b-collapse
                v-bind:id="'cat-' + index"
                accordion="my-accordion"
                role="tabpanel"
              >
                <b-card-body class="p-1 m-0">
                  <div class="accordion p-0 m-0" role="tablist">
                    <b-card
                      no-body
                      class="p-1 m-0"
                      v-for="(data, index) in itemsub"
                      v-bind:key="data.Marca"
                    >
                      <b-card-header
                        header-tag="header"
                        class="p-0 m-0"
                        role="tab"
                      >
                        <b-button
                          class="tamanof"
                          block
                          v-b-toggle="'subcat-' + index"
                          variant="light"
                          v-on:click="refresca2(data)"
                          ><h5 align="left">{{ data }}</h5></b-button
                        >
                      </b-card-header>
                    </b-card>
                  </div>
                </b-card-body>
              </b-collapse>
            </b-card>
          </div>
        </b-col>
        <b-col cols="8">
          <div>
            <div v-show="show">
              <Carrusel />
            </div>
            <div class="clearfix">
              <h6 class="m-2 float-left">
                <strong>Selección: {{ categor }} / {{ marc }}</strong>
              </h6>
              <b-button
                id="botoncarro"
                size="sm"
                class="float-right mr-3 mt-1"
                v-on:click="refresca3(marc)"
                v-b-modal.modal-2
              >
                <b-icon icon="cart4" aria-hidden="true"></b-icon>
                Carro Compras
              </b-button>
            </div>
            <b-card-group columns>
              <b-card
                tag="article"
                style="max-width: 20rem"
                class="m-2 text-center"
                v-for="data in itemproducto"
                v-bind:key="data.id"
              >
                <b-card-header>
                  <b-button
                    v-b-modal.modal-1
                    variant="outline-light"
                    v-on:click="verProducto(data)"
                    class="text-center"
                  >
                    <img
                      v-bind:src="'img/' + data.id + '.jpg'"
                      class="img-fluid"
                      width="200px"
                      v-bind:alt="data.id"
                      img-top
                    />
                  </b-button>
                </b-card-header>

                <b-card-text class="text-center">
                  {{ data.Nombre }}
                </b-card-text>

                <b-button
                  v-b-modal.modal-1
                  variant="success"
                  v-on:click="verProducto(data)"
                  class="text-center"
                  >{{ Intl.NumberFormat().format(data.precio) }}</b-button
                >
              </b-card>
            </b-card-group>
          </div>
        </b-col>
      </b-row>
    </b-container>

    <b-modal id="modal-1">
      <template #modal-header="{ close }">
        <!-- Emulate built in modal header close button action -->
        <h6>{{ nombre }}</h6>
        <b-button size="sm" variant="outline-danger" @click="close()">
          Volver
        </b-button>
      </template>
      <b-row>
        <b-col class="text-center" cols="4">
          <div>
            <b-img
              v-bind:src="'img/' + imagen"
              fluid-grow
              alt="Imagen adaptable"
            >
            </b-img>
          </div>

          <h4>
            <strong>{{ Intl.NumberFormat().format(precio) }}</strong>
          </h4>
        </b-col>
        <b-col cols="8">
          <span class="my-4">{{ descripcion }}</span>
          <br />
          <br />
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="4">
          <label class="text-left">Cantidad:</label>
          <b-form-input
            id="type-number"
            type="number"
            min="0"
            v-model="cant"
            autofocus
            class="form-control text-right"
            v-on:input="calculatotal()"
          ></b-form-input>
        </b-col>
        <b-col cols="8" class="text-center">
          <label><strong>Total:</strong></label>
          <h4>
            <strong>{{ Intl.NumberFormat().format(total) }}</strong>
          </h4>
        </b-col>
      </b-row>
      <template #modal-footer>
        <!-- Emulate built in modal header close button action -->

        <b-button
          id="botonA"
          size="sm"
          variant="outline-success"
          autofocus
          v-on:click="agregacompra(this)"
        >
          <b-icon icon="cart4" aria-hidden="true"></b-icon>
          Añardir al Carrito de Compras
        </b-button>
      </template>
    </b-modal>

    <b-modal id="modal-2" size="lg">
      <template #modal-header="{ close }">
        <!-- Emulate built in modal header close button action -->
        <h6>
          <b-icon icon="cart4" aria-hidden="true"></b-icon> Carro de Compras
        </h6>
        <b-button
          id="botoncierre"
          size="sm"
          variant="outline-danger"
          v-on:click="close()"
        >
          Salir sin Comprar
        </b-button>
      </template>
      <b-row>
        <b-col cols="1">
         
        </b-col>
        <b-col cols="5">
          <h5><strong> Producto</strong></h5>
        </b-col>
        <b-col cols="1" align="center">
          <h5><strong> Cant</strong></h5>
        </b-col>
        <b-col cols="2" align="right">
          <h5><strong> Precio</strong></h5>
        </b-col>
        <b-col cols="2" align="right">
          <h5><strong> Total</strong></h5>
        </b-col>
      </b-row>

       

      
          <b-row v-for="item1 in cart" v-bind:key="item1.id">
            <b-col cols="1">
              <b-button variant="link" 
              size="sm"  
              v-b-tooltip.hover 
              title="Click para Eiminar Producto del Pedido"
              v-on:click="removeItemFromArr()">
              <h5> <b-icon icon="trash" aria-hidden="true"></b-icon> </h5>
              </b-button>
            </b-col>
            <b-col cols="5">
              <h5>{{ item1.nombre }}</h5></b-col
            >
             <b-col cols="1" align="center"
              ><h5>{{ item1.cantidad }}</h5></b-col
            >
             <b-col cols="2" align="right"
              ><h5>{{ Intl.NumberFormat().format(item1.precio) }}</h5></b-col
            >
             <b-col cols="2" align="right"
              ><h5>{{ Intl.NumberFormat().format(item1.totalxprod)}}</h5></b-col
            >
          </b-row>
     

      <template #modal-footer="{ ok }">
        <!-- Emulate built in modal header close button action -->
        <b-row align-content="center">
          <b-col cols="7"  >
            <h6  >
              <strong>
                Total Pedido :
                {{ Intl.NumberFormat().format(carrototalcarro) }}</strong
              >
            </h6>
          </b-col  >
          <b-col cols="5" align="center">
            <b-button
              size="sm"
             
              variant="outline-danger"
              @click="ok()"
            >
              <b-icon icon="cart4" aria-hidden="true"></b-icon>
              Confirmar Pedido (DESACTIVADO _ PRONTO HABILITADO)
            </b-button>
          </b-col>
        </b-row>
      </template>
    </b-modal>
  </div>
</template>

<script>
import Carrusel from "@/components/Carrusel.vue";

export default {
  name: "Siembra",
  components: {
    Carrusel,
  },
  data() {
    return {
      siembraDataList: [],
      text: "Prueba de modal",
      categor: "Perros",
      marc: "Camas",
      nombre: "",
      descripcion: "",
      precio: 0,
      cant: 0,
      total: 0,
      carroid: [],
      carronombre: [],
      carroprecio: [],
      carrocant: [],
      carrototalxprod: [],
      carrototalcarro: 0,
      imagen: "",
      ident: "",
      mensajecarro: "No hay productos Agregados al carro de compras",
      cont: 0,
      show: true,
      cart: []
    };
  },
  methods: {
    getSiembraData() {
      fetch("csvjson.json")
        .then((response) => response.json())
        .then((data) => (this.siembraDataList = data))

        .catch(function () {
          alert("Can't connect to backend try latter");
        });
    },
    refresca(val) {
      this.categor = val;
    },
    refresca2(val2) {
      this.marc = val2;

      window.scrollTo(0, 0);
      this.show = false;
    },
    refresca3(val2) {
      this.marc = "";
      this.marc = val2;
      const array1 = this.carrototalxprod;
      const reducer = (accumulator, currentValue) => accumulator + currentValue;
      this.carrototalcarro = array1.reduce(reducer);
    },

    removeItemFromArr () {
      
      
      //this.cart.splice(,1);
      

     
  
    },

    verProducto(val4) {
      this.nombre = val4.Nombre;
      this.descripcion = val4.Descripción;
      this.precio = val4.precio;
      this.total = val4.precio;
      this.imagen = val4.id + ".jpg";
      this.ident = val4.id;
    },
    calculatotal() {
      this.total = this.cant * this.precio;
    },
    agregacompra() {
      this.carroid[this.cont] = this.ident;

      this.carronombre[this.cont] = this.nombre;
      this.carroprecio[this.cont] = this.precio;
      this.carrocant[this.cont] = this.cant;
      this.carrototalxprod[this.cont] = this.total;
      this.total = this.cant * this.precio;
      this.cont = this.cont + 1;
      
     
       this.cart.push({
                n:this.cont,
                id:this.ident,
                nombre :this.nombre,
                cantidad:this.cant,
                precio:this.precio,
                totalxprod:this.total,
                
            })

     

       this.$bvModal.hide("modal-1");
    },
  },
  mounted() {
    //capturo toda la base
    this.getSiembraData();
    this.$root.$on("bv::modal::shown", () => {
      this.cant = 1;
    });
    if (localStorage.carrototalcarro) {
      this.carrototalcarro = localStorage.carrototalcarro;
    }
  },

  computed: {
    //capturo solo las categorias
    items() {
      return this.siembraDataList
        .map((item) => {
          return item.Categoría;
        })
        .reduce(
          (newTempArr, el) =>
            newTempArr.includes(el) ? newTempArr : [...newTempArr, el],
          []
        );
    },
    itemsub() {
      return this.siembraDataList
        .map((item) => {
          return item;
        })
        .filter((x) => x.Categoría == this.categor)
        .map((item) => {
          return item.Marca;
        })
        .reduce(
          (newTempArr, el) =>
            newTempArr.includes(el) ? newTempArr : [...newTempArr, el],
          []
        );
    },

    itemproducto() {
      return this.siembraDataList
        .map((item) => {
          return item;
        })
        .filter((x) => x.Categoría == this.categor && x.Marca == this.marc);
    },
    itemproducto2() {
      return this.siembraDataList
        .map((item) => {
          return item;
        })
        .filter((x) => x.Categoría == this.categor);
    },
  },
};
</script>


<style scoped>
@media (max-width: 640px) {
  .tamanof {
    font-size: 0.6rem !important;
  } /*1rem = 16px*/
}

h6 {
  font-size: 1.2rem !important;
  color: rgb(239, 63, 37);
} /*1rem = 16px*/

@media (max-width: 640px) {
  h6 {
    font-size: 0.9rem !important;
    color: rgb(239, 63, 37);
  } /*1rem = 16px*/
}

@media (max-width: 640px) {
  h5 {
    font-size: 0.65rem !important;
  } /*1rem = 16px*/
}
</style>




